import React, { useContext } from 'react'
import { Container } from 'react-bootstrap';
import carContext from '../modules/car-context';
import { formatCurrency } from '../modules/string';


const CartResume = () => {


    const {cart, changeQty} = useContext(carContext);

    function handleChangeQty(e){
        console.dir(e.target.value)
    }
    
    return (
        <Container>
            <h1>Carrito de Compras</h1>
            <table width='100%' cellSpacing={0} cellPadding={'20px'}>
                <thead>
                    <tr>
                        <th>Imagen</th>
                        <th>Producto</th>
                        <th>Precio</th>
                        <th>Cantidad</th>
                        <th>Subtotal</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                {
                    cart.map((elem) =>{
                        return(
                            
                            <tr key={elem.id}>
                                <td><img src={elem.photo} width='160px'></img></td>
                                <tr>
                                    <tr>
                                        <td>{`${elem.name} ${elem.type} - ${elem.size}`}</td>
                                    </tr>
                                    <tr>
                                        <td>{elem.ingredientes}</td>
                                    </tr>
                                </tr>
                                
                                <td>{elem.price}</td>
                                <td>
                                    <input type='text' value={elem.qty} Onchange={handleChangeQty} style={{width:'50px'}}/>
                                </td>
                                <td>{formatCurrency(elem.price * elem.qty)}</td>
                            </tr>
                                
                            
                        )
                    })
                }
                </tbody>
            </table>

            {/* {context.cart.map((elem) =>{
                return(
                    <div>
                        {`${elem.name} ${elem.type} - ${elem.size}`}
                    </div>
                )
            })} */}
        </Container>
    )
}

export default CartResume
