import {useState} from 'react'

export default function useCart(){

    const [cart, setCart] = useState([]);

    function addToCart(product){
        setCart((prev) => {
            const existing = cart.find(
                (item) => item.id === product.id,
                );

            return existing
            ? [
                ...cart.map((item) =>
                item.id === product.id
                    ? { ...item, qty: item.qty + 1 }
                    : item,
                ),
            ]
            : [...prev, {...product, qty: 1}];
        });
    }
    
    function changeQty(product, qty){
        setCart((prev) => 
            [
                ...prev.map((item) =>
                item.id === product.id
                    ? { ...item, qty }
                    : item,
                ),
            ]
        );
            
    }

    return {
        cart,
        addToCart,
        changeQty,
    }
}