import React, {useContext} from 'react';
import logo from '../images/logo@2x.png'
import { Navbar, Nav, Container } from 'react-bootstrap';
import '../styles/Navigation.css'
import {Link} from 'react-router-dom'
import carContext from '../modules/car-context';





const Navegacion = () => {
    const ctx = useContext(carContext);
  return (
    <Container  id="header">
        <Navbar collapseOnSelect expand="lg" className="px-3 mt-2"  id="navbar">
            <Navbar.Brand href="#">
                <Link to="/deploy-store">
                    <img src={logo} alt="restaurant_logo" width="110px"/>
                </Link>
            </Navbar.Brand>

            <Navbar.Toggle aria-controls="responsive-navbar-nav" />

            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="ms-auto">
                    <Link className="nav-link" to="/deploy-store">Inicio</Link>
                    <Link className="nav-link" to="/deploy-store/About">Nosotros</Link>
                    <Link className="nav-link" to="/deploy-store/Menu">Men√∫</Link>
                    <Link className="nav-link active" to="/deploy-store/Services">Servicios</Link>
                    <Link className="nav-link" to="/deploy-store/Contact">Cont√°ctanos</Link>
                    <Link className="nav-link" to="/deploy-store/Cart">Cart: {ctx.cart.reduce((acc, item) => acc + item.qty ,0)}</Link>
                </Nav>
            </Navbar.Collapse>
        
        </Navbar>
    </Container>
    
  );
}

export default Navegacion;